.slider-programs {
  position: relative;

  width: 100%;
  margin: 45px 0 0;

  overflow: hidden;

  @include vp-1199 {
    padding: 0 45px;
    margin: 38px 0 0;
  }

  @include vp-767 {
    padding: 0;
    margin: 0;
  }

  .slider-programs__bullet {
    top: auto;
    bottom: 0;

    width: calc(91% + 5px);
    height: 3px;

    background-color: rgba($color-cerulean-blue, 0.2);

    @include vp-1199 {
      width: 73%;
      left: 45px;
      bottom: -1px;
    }

    @include vp-767 {
      display: none;
    }
  }
}

.slider-programs__list {
  padding: 0;
  height: auto;
  margin: 0 0 51px;
  list-style-type: none;

  @include vp-767 {
    margin: 0 0 28px;
  }
}

.slider-programs__item {
  display: flex;
  align-items: flex-end;
  min-height: 420px;
  height: auto;
  padding: 0;

  background-color: rgba($color-cerulean-blue, 0.2);
  border-radius: 12px;

  @include vp-1199 {
    width: 324px;
  }

  @include vp-767 {
    min-height: 400px;
  }

  picture {
    position: relative;

    width: 100%;
    height: 100%;
  }
}

.slider-programs__image {
  position: relative;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;

  border-radius: 12px;
  object-fit: cover;
  z-index: -1;
}

.slider-programs__content-slide {
  position: absolute;
  padding: 20px 20px 21px;
  max-height: 420px;

  overflow-y: scroll;


  &::-webkit-scrollbar {
    display: none;
  }

  @include vp-1199 {
    padding: 20px 20px 19px;
  }

  @include vp-767 {
    padding: 20px 10px 19px;
  }

  h3 {
    position: relative;

    font-family: "Manrope", "Arial", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 26px;
    color: $color-cerulean-blue;
    letter-spacing: -1px;

    width: 194px;
    padding: 13px 0 2px 18px;
    margin: 0;

    background-color: $color-default-white;
    border-radius: 12px 12px 0 0;

    @include vp-1199 {
      font-size: 20px;
      line-height: 24px;
      letter-spacing: -0.7px;

      width: 173px;
      padding: 14px 0 0 18px;
    }

    @include vp-767 {
      font-size: 16px;
      line-height: 18px;
      letter-spacing: -0.7px;

      width: 54%;
      padding: 19px 0 5px 18px;
    }

    &::after {
      position: absolute;
      bottom: 0;
      right: -18px;

      width: 18px;
      height: 18px;

      background-color: $color-transparent;
      background-image: url("../img/svg/angle-small.svg");
      background-repeat: no-repeat;
      content: "";

      @include vp-1199 {
        bottom: -3px;
        right: -20px;

        width: 20px;
        height: 20px;
      }

      @include vp-767 {
        bottom: -1px;
        right: -18px;

        width: 18px;
        height: 18px;
      }
    }
  }
}

.slider-programs__under-wrapper {
  display: flex;
  align-items: center;
  min-height: 78px;
  width: 91%;
  padding: 10px 18px 14px;

  background-color: $color-default-white;
  border-radius: 0 12px 12px 12px;

  @include vp-1199 {
    width: 263px;
    padding: 9px 18px 12px;
  }

  @include vp-767 {
    width: 100%;
    min-height: auto;
    padding: 12px 18px 15px;
  }

  p {
    font-family: "Manrope", "Arial", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: $color-charcoal;
    letter-spacing: -0.5px;

    padding: 0;
    margin: 0 11px 0 0;

    background-color: $color-default-white;

    @include vp-1199 {
      line-height: 19px;
    }

    @include vp-767 {
      font-size: 12px;
      line-height: 14px;
    }
  }
}

.slider-programs__bullet {
  .swiper-pagination-progressbar-fill {
    background-color: $color-cerulean-blue;
    opacity: 1;
  }
}
