  flex-direction: column;
  margin: 0 0 15px;
}
@media (max-width: 767px) {
  .feedback__input {
    margin: 0 0 21px;
  }
}
.feedback__input--textarea {
  margin: 0 0 19px;
}
@media (max-width: 767px) {
  .feedback__input--textarea {
    margin: 0 0 20px;
  }
}
.feedback__input input[type=text],
.feedback__input input[type=tel],
.feedback__input textarea {
  font-family: "Manrope", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 26px;
  color: #ffffff;
  width: 100%;
  padding: 11px 25px;
  background-color: rgba(255, 255, 255, 0);
  border: 1px rgba(255, 255, 255, 0.3) solid;
  border-radius: 8px;
}
@media (hover: hover) {
  .feedback__input input[type=text]:not(:disabled):hover:not(.focus-visible),
.feedback__input input[type=tel]:not(:disabled):hover:not(.focus-visible),
.feedback__input textarea:not(:disabled):hover:not(.focus-visible) {
    border: 1px #ffffff solid;
    transition: all 0.2s;
  }
  .feedback__input input[type=text]:not(:disabled):hover:not(.focus-visible) + .feedback__label,
.feedback__input input[type=tel]:not(:disabled):hover:not(.focus-visible) + .feedback__label,
.feedback__input textarea:not(:disabled):hover:not(.focus-visible) + .feedback__label {
    opacity: 1;
    transition: all 0.2s;
  }
}
.feedback__input input[type=text]:not(:disabled).focus-visible:focus,
.feedback__input input[type=tel]:not(:disabled).focus-visible:focus,
.feedback__input textarea:not(:disabled).focus-visible:focus {
  border: 1px #ffffff solid;
  outline: none;
}
.feedback__input input[type=text]:not(:disabled).focus-visible:focus + .feedback__label,
.feedback__input input[type=tel]:not(:disabled).focus-visible:focus + .feedback__label,
.feedback__input textarea:not(:disabled).focus-visible:focus + .feedback__label {
  opacity: 1;
}
.feedback__input input[type=text]:not(:disabled).focus-visible:active,
.feedback__input input[type=tel]:not(:disabled).focus-visible:active,
.feedback__input textarea:not(:disabled).focus-visible:active {
  border: 1px #ffffff solid;
  outline: none;
}
.feedback__input input[type=text]:not(:disabled).focus-visible:active + .feedback__label,
.feedback__input input[type=tel]:not(:disabled).focus-visible:active + .feedback__label,
.feedback__input textarea:not(:disabled).focus-visible:active + .feedback__label {
  opacity: 1;
}
.feedback__input input[type=text]:not(:disabled):active:not(.focus-visible),
.feedback__input input[type=tel]:not(:disabled):active:not(.focus-visible),
.feedback__input textarea:not(:disabled):active:not(.focus-visible) {
  border: 1px #ffffff solid;
  outline: none;
}
.feedback__input input[type=text]:not(:disabled):active:not(.focus-visible) + .feedback__label,
.feedback__input input[type=tel]:not(:disabled):active:not(.focus-visible) + .feedback__label,
.feedback__input textarea:not(:disabled):active:not(.focus-visible) + .feedback__label {
  opacity: 1;
}
.feedback__input input[type=text]:disabled,
.feedback__input input[type=tel]:disabled,
.feedback__input textarea:disabled {
  background-color: rgba(255, 255, 255, 0.2);
  border: 1px #ffffff solid;
  opacity: 0.2;
}
.feedback__input input[type=text]:disabled + .feedback__label,
.feedback__input input[type=tel]:disabled + .feedback__label,
.feedback__input textarea:disabled + .feedback__label {
  opacity: 0.6;
}
.feedback__input input[aria-invalid=true],
.feedback__input textarea[aria-invalid=true] {
  border: 1px #ff5e66 solid;
  background-color: rgba(255, 94, 102, 0.2);
}
.feedback__input textarea {
  height: 96px;
}
@media (max-width: 767px) {
  .feedback__input textarea {
    height: 93px;
  }
}

.feedback__label {
  font-family: "Manrope", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #f0efe9;
  letter-spacing: -0.5px;
  display: block;
  margin: 0 0 9px;
  opacity: 0.8;
  order: -1;
}
@media (max-width: 767px) {
  .feedback__label {
    font-size: 12px;
    line-height: 14px;
    margin: 0 0 12px;
  }
}

.feedback__submit {
  width: 100%;
}
@media (max-width: 767px) {
  .feedback__submit {
    padding: 3px 4px 3px 19px;
  }
}

.main-footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1440px;
  padding: 50px 100px;
  margin: 0 auto;
  gap: 26px;
  flex-wrap: wrap;
}
@media (max-width: 1199px) {
  .main-footer {
    padding: 35px 45px 50px;
  }
}
@media (max-width: 767px) {
  .main-footer {
    padding: 14px 15px 40px;
    gap: 15px;
  }
}

.main-footer__wrapper-primary {
  display: flex;
  align-items: center;
  gap: 51px;
}
@media (max-width: 1199px) {
  .main-footer__wrapper-primary {
    width: 100%;
    justify-content: space-between;
    gap: 0;
  }
}
@media (max-width: 767px) {
  .main-footer__wrapper-primary {
    flex-direction: column;
    gap: 13px;
  }
}

.main-footer__nav-list {
  display: flex;
  max-width: 700px;
  padding: 0;
  margin: 0;
  row-gap: 10px;
  flex-wrap: wrap;
  list-style-type: none;
}
@media (max-width: 1199px) {
  .main-footer__nav-list {
    max-width: 548px;
  }
}
@media (max-width: 767px) {
  .main-footer__nav-list {
    justify-content: center;
    max-width: none;
    column-gap: 31px;
    row-gap: 5px;
  }
}

.main-footer__nav-item:not(:last-child) {
  margin-right: 30px;
}
@media (max-width: 1199px) {
  .main-footer__nav-item:not(:last-child) {
    margin-right: 16px;
  }
}
@media (max-width: 767px) {
  .main-footer__nav-item:not(:last-child) {
    margin-right: 0;
  }
}

.main-footer__nav-link {
  font-family: "Manrope", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 26px;
  color: #484848;
  letter-spacing: -0.5px;
}
@media (max-width: 767px) {
  .main-footer__nav-link {
    font-size: 14px;
    line-height: 20px;
    letter-spacing: -0.5px;
  }
}
@media (hover: hover) {
  .main-footer__nav-link:not(.disabled):hover:not(.focus-visible) {
    color: #316dc2;
    transition: all 0.2s;
  }
}
.main-footer__nav-link:not(.disabled).focus-visible:focus {
  color: #316dc2;
  transition: all 0.2s;
}
.main-footer__nav-link:not(.disabled).focus-visible:active {
  color: #275699;
  transition: all 0.2s;
}
.main-footer__nav-link:not(.disabled):active:not(.focus-visible) {
  color: #275699;
  transition: all 0.2s;
}
.main-footer__nav-link.disabled {
  opacity: 0.3;
  pointer-events: none;
}

.main-footer__social-item:not(:last-child) {
  margin-right: 20px;
}
@media (max-width: 1199px) {
  .main-footer__social-item:not(:last-child) {
    margin-right: 10px;
  }
}

.main-footer__social-list {
  display: flex;
  max-width: 170px;
  padding: 0;
  margin: 0;
  list-style-type: none;
  row-gap: 15px;
  flex-wrap: wrap;
}
@media (max-width: 1199px) {
  .main-footer__social-list {
    max-width: 360px;
    order: 1;
  }
}
@media (max-width: 767px) {
  .main-footer__social-list {
    width: 100%;
    max-width: none;
    justify-content: center;
  }
}

.main-footer__social-link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 36px;
  height: 36px;
  background-color: #316dc2;
  border-radius: 50%;
}
@media (max-width: 767px) {
  .main-footer__social-link {
    width: 26px;
    height: 26px;
  }
}
.main-footer__social-link svg {
  fill: #ffffff;
}
@media (max-width: 767px) {
  .main-footer__social-link svg {
    width: 12px;
    height: 12px;
  }
}
@media (hover: hover) {
  .main-footer__social-link:not(.disabled):hover:not(.focus-visible) {
    outline: none;
  }
  .main-footer__social-link:not(.disabled):hover:not(.focus-visible) svg {
    opacity: 0.5;
    transition: all 0.2s;
  }
}
.main-footer__social-link:not(.disabled).focus-visible:focus {
  outline: none;
}
.main-footer__social-link:not(.disabled).focus-visible:focus svg {
  opacity: 0.5;
  transition: all 0.2s;
}
.main-footer__social-link:not(.disabled).focus-visible:active {
  background-color: #275699;
  transition: all 0.2s;
}
.main-footer__social-link:not(.disabled).focus-visible:active svg {
  opacity: 1;
  transition: all 0.2s;
}
.main-footer__social-link:not(.disabled):active:not(.focus-visible) {
  background-color: #275699;
  transition: all 0.2s;
}
.main-footer__social-link:not(.disabled):active:not(.focus-visible) svg {
  opacity: 1;
  transition: all 0.2s;
}
.main-footer__social-link.disabled {
  opacity: 0.2;
  pointer-events: none;
}

.main-footer__wrapper-secondary {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  flex-wrap: wrap;
}
@media (max-width: 1199px) {
  .main-footer__wrapper-secondary {
    flex-direction: column;
    width: max-content;
    max-width: 500px;
    align-items: start;
  }
}
@media (max-width: 767px) {
  .main-footer__wrapper-secondary {
    text-align: center;
    max-width: 85vw;
    margin: 0 auto;
  }
}

.main-footer__copyright,
.main-footer__copyright-link {
  font-family: "Manrope", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #484848;
  letter-spacing: -0.5px;
}
@media (max-width: 1199px) {
  .main-footer__copyright,
.main-footer__copyright-link {
    font-size: 12px;
    line-height: 12px;
  }
}

@media (max-width: 1199px) {
  .main-footer__copyright {
    opacity: 0.6;
    order: 1;
  }
}
@media (max-width: 767px) {
  .main-footer__copyright {
    margin: 0 auto;
  }
}

.main-footer__copyright-link {
  letter-spacing: -0.4px;
  border-bottom: 1px #484848 solid;
}
@media (max-width: 1199px) {
  .main-footer__copyright-link {
    margin: 0 0 10px;
  }
}
@media (max-width: 767px) {
  .main-footer__copyright-link {
    padding: 0 0 1px;
    margin: 1px auto 12px;
  }
}
@media (hover: hover) {
  .main-footer__copyright-link:not(.disabled):hover:not(.focus-visible) {
    color: #316dc2;
    border-bottom: 1px #316dc2 solid;
    transition: all 0.2s;
  }
}
.main-footer__copyright-link:not(.disabled).focus-visible:focus {
  color: #316dc2;
  border-bottom: 1px #316dc2 solid;
  transition: all 0.2s;
}
.main-footer__copyright-link:not(.disabled).focus-visible:active {
  color: #275699;
  border-bottom: 1px #275699 solid;
  transition: all 0.2s;
}
.main-footer__copyright-link:not(.disabled):active:not(.focus-visible) {
  color: #275699;
  border-bottom: 1px #275699 solid;
  transition: all 0.2s;
}
.main-footer__copyright-link.disabled {
  opacity: 0.2;
  pointer-events: none;
}

